import React, { Component } from 'react';
import '../css/cosmo.css'
import { Button, Header, Icon, Image, Modal } from 'semantic-ui-react';

export default class ModalPopUp extends Component {

  constructor(props) {
    super(props);
    this.state = {clicked_index: 0};
    this.clickHandler = this.clickHandler.bind(this);
  }
  
  clickHandler(index){
    console.log(index)
      this.setState({clicked_index: index});
  };

  render() {


    const ModalScrolling = () => (
  
      <Modal style={{marginLeft: "20em"}} trigger={<Button className="circular ui button" style={{padding: "1em", marginLeft: "2%"}}>
                    <i className="info icon" style={{color: "#575A89", paddingLeft: "50%"}}></i>
                    </Button>}>
        <Modal.Header>Profile Picture</Modal.Header>
        <Modal.Content image>
          <Image wrapped size='medium' src='https://react.semantic-ui.com/images/wireframe/image.png' />
          <Modal.Description>
            <Header>Modal Header</Header>
            <p>
              This is an example of expanded content that will cause the modal's
              dimmer to scroll
            </p>
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
            <Image src='https://react.semantic-ui.com/images/wireframe/paragraph.png' />
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button primary>
            Proceed <Icon name='right chevron' />
          </Button>
        </Modal.Actions>
      </Modal>
    )
    console.log("it works");

    return (
      <div>
        <ModalScrolling />
      </div>
    )
  }
}